import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import '../styles/SignupPage.css'
import { signup } from '../services/userService'
import { errorToast, signupSuccessToast } from '../utils/toast'

const SignupPage = () => {

  const navigate = useNavigate()

  const handleSubmit = async(e) => {
    e.preventDefault()
    const username = e.target.username.value
    const email = e.target.email.value
    const password = e.target.password.value
    const repassword = e.target.repassword.value

    if (username.length < 8) {
      errorToast("Tn ng nhp phi c t nht 8 k t")
      return
    }

    if (password !== repassword) {
      errorToast('Mt khu nhp li khng trng khp')
      return
    }

    const response = await signup(username, email, password)
    if (response === "username exists") {
      errorToast('Tn ng nhp  c s dng')
    } else if (response === "email exists") {
      errorToast('Email ny  c ng k bi mt ti khon khc')
    } else if (response === "signup success") {
      setTimeout(() => navigate('/login/'), 2000)
      signupSuccessToast()
    }
  }

  return (
    <div class="signup-page">
      <h2>ng k</h2>
      <form onSubmit={handleSubmit} autoComplete="off" spellCheck={false}>
        <div class="input-box">
          <input type="text" placeholder="Tn ng nhp" name="username" required/>
        </div>
        <div class="input-box">
          <input type="email" placeholder="Email" name="email" required/>
        </div>
        <div class="input-box">
          <input type="password" placeholder="Mt khu" name="password" required/>
        </div>
        <div class="input-box">
          <input type="password" placeholder="Nhp li mt khu" name="repassword" required/>
        </div>
        <div class="policy">
          <input type="checkbox" required/>
          <h3>Ti chp nhn mi iu khon v hp ng</h3>
        </div>
        <div class="input-box button">
          <input type="Submit" value="ng k"/>
        </div>
        <div class="text">
          <h3>Bn  c ti khon? <Link to='/login/'>ng nhp ngay</Link></h3>
        </div>
      </form>
    </div>
  )
}

export default SignupPage