import { toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';

export const removeCartItemToast = (productName) => {
  const message = 'Xa "' + productName + '" khi gi hng'
  toast.info(message);
}

export const addCartItemToast = (productName) => {
  const message = 'Thm "' + productName + '" vo gi hng'
  toast.success(message);
}

export const loginFirstToast = () => {
  toast.info("Hy ng nhp trc");
}

export const endSessionToast = () => {
  toast.warn("Phin ng nhp  ht hn");
}

export const signupSuccessToast = () => {
  toast.success('ng k thnh cng');
}

export const errorToast = (message) => {
  toast.error(message);
}
